    handleCheckAllChange(val) {
      console.log(val);
      this.all=!val
      if (val == false) {
        this.bar1 = false;
        this.bar2 = false;
        this.bar3 = false;
        this.bar4 = false;
        this.bar5 = false;

        this.show_data = {};
      } else {
