        this.bar2 = true;
        this.bar3 = true;
        this.bar4 = true;
        this.bar5 = true;

      }
      this.checkedCities = val ? cityOptions : [];
      this.isIndeterminate = false;
    },
    handleCheckedCitiesChange(value) {
      console.log("1");
      console.log(value);
      if(value.length==5){
        this.all=false
      }else{
